.toggle {
  z-index: 3;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  width: 134px;
  height: 54px;
  padding: 0 3px 0 6px;
  border: 1px solid #316dc2;
  border-radius: 28px;
  outline: none;
  cursor: pointer;
}
.toggle:hover, .toggle:focus, .toggle:focus-visible {
  background-color: #316dc2;
  outline: none;
}
.toggle:hover span, .toggle:focus span, .toggle:focus-visible span {
  color: #ffffff;
  outline: none;
}
.toggle:hover svg, .toggle:focus svg, .toggle:focus-visible svg {
  color: #316dc2;
  background-color: #ffffff;
  outline: none;
}
.toggle:active {
  background-color: #316dc2;
}
.toggle:active span {
  color: #ffffff;
  opacity: 0.7;
}
.toggle:active svg {
  color: rgba(49, 109, 194, 0.7);
  background-color: #ffffff;
}
.toggle:disabled {
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #a9afb7;
  pointer-events: none;
}
.toggle:disabled span {
  color: #a9afb7;
}
.toggle:disabled svg {
  background-color: #a9afb7;
  color: rgba(169, 175, 183, 0.6);
}
.toggle span {
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #316dc2;
  padding: 13px;
}
.toggle svg {
  height: 46px;
  width: 46px;
  color: #ffffff;
  background-color: #316dc2;
  padding: 12px;
  border-radius: 50%;
}

.toggle.is-opened {
  width: 286px;
  background-color: #316dc2;
}
.toggle.is-opened span {
  color: #ffffff;
}
.toggle.is-opened svg {
  color: #316dc2;
  background-color: #ffffff;
}
@media (max-width: 1199px) {
  .toggle.is-opened {
    min-width: 286px;
    width: 84%;
  }
}
@media (max-width: 767px) {
  .toggle.is-opened {
    left: 15px;
    width: auto;
    right: 0;
  }
}
.toggle.is-opened:hover, .toggle.is-opened:focus, .toggle.is-opened:focus-visible {
  background-color: #ffffff;
  border: none;
  outline: none;
}
.toggle.is-opened:hover span, .toggle.is-opened:focus span, .toggle.is-opened:focus-visible span {
  color: #316dc2;
  outline: none;
}
.toggle.is-opened:hover svg, .toggle.is-opened:focus svg, .toggle.is-opened:focus-visible svg {
  color: #ffffff;
  background-color: #316dc2;
  outline: none;
}
.toggle.is-opened:active {
  background-color: #ffffff;
  border: none;
}
.toggle.is-opened:active span {
  color: #316dc2;
  opacity: 0.7;
}
.toggle.is-opened:active svg {
  background-color: #316dc2;
  color: rgba(255, 255, 255, 0.7);
}
.toggle.is-opened:disabled {
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #a9afb7;
  pointer-events: none;
}
.toggle.is-opened:disabled span {
  color: #a9afb7;
}
.toggle.is-opened:disabled svg {
  background-color: #a9afb7;
  color: rgba(169, 175, 183, 0.6);
}

.toggle .toggle__closed {
  display: none;
  opacity: 0;
}

.toggle.is-opened .toggle__opened {
  display: none;
}

.toggle.is-opened .toggle__closed {
  display: inline;
  opacity: 1;
  color: #316dc2;
}

